@import url("https://fonts.googleapis.com/css2?family=Lato&family=Noto+Sans+TC&family=Roboto&family=Rubik&display=swap");
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: '';
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

img {
  vertical-align: middle;
}

a {
  text-decoration: none;
  color: #000000;
}

html, body {
  margin: 0 auto;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Microsoft JhengHei", Roboto, "Helvetica Neue", Arial, sans-serif;
}

*, *::after, *::before {
  box-sizing: border-box;
}

body {
  font-family: 'Lato', sans-serif;
}

.index__h1 {
  color: #00cc99;
  font-size: 20px;
  font-weight: 400;
}

nav {
  background-color: #F8F9FA;
  max-width: 25%;
  height: 100vh;
  position: relative;
}

nav .logo {
  height: 40px;
  margin-top: 32px;
  margin-left: 32px;
  margin-bottom: 32px;
}

.navbar ul {
  width: 100%;
}

.navbar a {
  width: 100%;
  font-size: 16px;
  line-height: 1.5;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-left: 32px;
}

.navbar li {
  width: 100%;
}

.navbar li p {
  display: block;
  width: 85%;
}

.user {
  position: absolute;
  display: flex;
  align-items: center;
  width: 100%;
  padding-left: 32px;
  bottom: 32px;
}

.user img {
  width: 40px;
  height: 40px;
  border-radius: 100%;
  margin-right: 16px;
}

header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-left: 32px;
  padding-right: 32px;
}

header h1 {
  color: #212529;
  font-size: 24px;
  font-weight: bold;
  margin-right: 50px;
  line-height: 33px;
}

header h2 {
  color: #212529;
  font-size: 16px;
  font-weight: bold;
  line-height: 1.5;
  cursor: pointer;
}

header .head-title {
  display: flex;
  align-items: center;
  height: 93px;
}

header .head-icon i {
  margin-right: 26px;
  cursor: pointer;
}

.arrow {
  font-weight: bold;
  font-size: 16px;
  height: 16px;
  color: #198754;
  margin-left: 10px;
}

section {
  padding-left: 32px;
  padding-right: 32px;
}

h4 {
  font-size: 16px;
  font-weight: bold;
  color: #212529;
  line-height: 1.5;
  cursor: pointer;
  margin-bottom: 6px;
  margin-top: 12px;
}

.table-title {
  width: 30%;
}

.table-title td {
  border: 1px solid #6C757D;
  width: 100%;
  padding: 12px;
  cursor: pointer;
}

.table-title td .genre {
  color: #6C757D;
  font-size: 12px;
  line-height: 2;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 4px;
}

.table-title td .main-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #212529;
}

.table-title td .main-title .writer {
  color: #6C757D;
  font-size: 12px;
  line-height: 2;
}

.table-title td:hover, .table-title td:focus {
  background-color: #F8F9FA;
}
